<template>
    <div class="main">

        <section>
            <!--            <router-link to="/coming_ac_valhalla">ComingAcValhalla</router-link>-->
            <!--            <router-link to="/coming_cyberpunk_2077">ComingCyberpunk2077</router-link>-->
            <h1>10,000 X 10,000</h1>
            <h3>MANTMAN Newsletter</h3>

            <input
                    class="subscribe_email_input"
                    placeholder="email"
                    id="email"
                    v-model="email"
                    type="email"
                    name="email"
            ><br>


            <button
                    :disabled="!isValidatedEmail"
                    v-on:click="onSubmitSubscribe()"
                    class="subscribe_button"
                    type="submit"
                    value="Submit"
            >
                SUBSCRIBE
            </button>
        </section>
    </div>
</template>

<script>
    import db from "../utils/firestore"

    export default {
        name: "MainScreen",
        data() {
            return {
                email: null,
                isValidatedEmail: false
            }
        },
        watch: {
            email(value) {
                this.email = value;
                this.validateEmail(value);
            }
        },
        methods: {
            onSubmitSubscribe() {
                console.log('onSubmitSubscribe');
                db.collection("subscribe_for_mantman")
                    .add({email: this.email, is_validate: false, un_subscribe: false})
                    .then(() => {
                        alert('Thank you *^^*');
                        this.email = null;
                        this.isValidatedEmail = false;
                    })
                    .catch((error) => {
                        alert('Error TT');
                        console.error("Error writing document: ", error);
                    });
            },
            validateEmail(value) {
                this.isValidatedEmail = (/^\w+([.-]?\w+)*@\w+([.-]?\w+)*(\.\w{2,3})+$/.test(value));
            },
        }
    }
</script>

