import Vue from 'vue'
import VueRouter from 'vue-router'

import ComingCyberpunk2077Screen from "../screens/ComingCyberpunk2077Screen";
import ComingAcValhallaScreen from "../screens/ComingAcValhallaScreen";
import MainScreen from "../screens/MainScreen";

Vue.use(VueRouter)

const routes = [
    {
        path: '/',
        name: 'Home',
        component: MainScreen
    },
    {
        path: '/coming_ac_valhalla',
        name: 'ComingAcValhalla',
        component: ComingAcValhallaScreen
    },
    {
        path: '/coming_cyberpunk_2077',
        name: 'ComingCyberpunk2077',
        component: ComingCyberpunk2077Screen
    },
]

const router = new VueRouter({
    mode: 'history',
    base: process.env.BASE_URL,
    routes
})

export default router
